@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&display=swap);
* {
    padding: 0;
    margin: 0;
}

/* 
    GENERAL
*/

body {
    font-family: 'Open Sans', sans-serif;
}

button:focus {
    outline: none !important;
}
  
/* 
    JUMBOTRON
*/

.jumbotron {
    font-family: 'Montserrat', sans-serif;
}

/* 
    TABLE
*/
table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
}

th {
    font-family: 'Montserrat', sans-serif;
}

